import * as types from '../types';
import { Campaign } from '../../types/campaign'

const getCampaigns = () => {
  return {
    type: types.FETCH_CAMPAIGNS
  }
}

const addCampaigns = (campaigns: Campaign[]) => {
  return {
    type: types.ADD_CAMPAIGNS,
    payload: campaigns
  }
}

export {
  getCampaigns,
  addCampaigns
}