import * as ActionTypes from '../action/types'
import { Campaign } from '../types/campaign'

export interface State {
  campaigns: Campaign[]
  fetching: boolean
  fetched: boolean
  error: any
}

const initialState: State = {
  campaigns: [],
  fetching: false,
  fetched: false,
  error: ''
}


const reducers = (state: State = initialState, action: any) => {
  switch (action.type) {
    case ActionTypes.FETCH_CAMPAIGNS:
      return {
        ...state,
        fetching: true
      }
    case ActionTypes.FETCH_CAMPAIGNS_SUCCESS:
      return {
        ...state,
        fetching: false,
        fetched: true,
        campaigns: action.results,
        error: ''
      }
    case ActionTypes.FETCH_CAMPAIGNS_ERROR:
      return {
        ...state,
        fetching: false,
        fetched: false,
        campaigns: [],
        error: action.error
      }
    case ActionTypes.ADD_CAMPAIGNS:
      return {
        ...state,
        fetching: true
      }
    case ActionTypes.ADD_CAMPAIGNS_SUCCESS:
      return {
        ...state,
        fetching: false,
        fetched: true,
        campaigns: action.results,
        error: ''
      }
    case ActionTypes.ADD_CAMPAIGNS_ERROR:
      return {
        ...state,
        fetching: false,
        fetched: false,
        campaigns: [],
        error: action.error
      }
    default:
      return state
  }
}

export default reducers
