import * as React from 'react'
import { Dispatch } from 'redux'
import { connect } from 'react-redux'
import { 
  Button,
  Col,
  Form,
  FormControl,
  InputGroup,
  Navbar,
  Row
} from 'react-bootstrap'

import { addCampaigns, getCampaigns } from '../../action/creators'
import List from "../../components/List"
import { Campaign } from "../../types/campaign"
import { State } from '../../reducers'
import '../../styles/index.scss'

declare global {
  interface Window { AddCampaigns: any; }
}

interface Props {
  fetching: boolean
  campaigns: Array<Campaign>
  dispatch: any
}

interface AppState {
  fetching: boolean
  campaigns: Array<Campaign>
}

class AppComponent extends React.Component<Props, AppState> {
  constructor(props: Props) {
    super(props)

    this.state = {
      campaigns: [],
      fetching: props.fetching,
    }

    window.AddCampaigns = (campaigns: Campaign[]) => {
      this.props.dispatch(addCampaigns(campaigns))
    }
  }

  componentDidMount() {
    this.props.dispatch(getCampaigns())
  }

  public componentWillReceiveProps(props: any) {
    if (props.campaigns) {
      this.setState({
        campaigns: props.campaigns,
        fetching: props.fetching
      })
    }
  }

  render() {
    const { campaigns, fetching } = this.state
    return (
      <div id="app">
        <Navbar>
          <Navbar.Brand href="#home">React Campaigns</Navbar.Brand>
        </Navbar>        
        <div id="wrapper">
          <Row>
            <Col>
              <div className="filters">
                <Row>
                  <Col md={5}>
                    <InputGroup className="mb-3">
                      <FormControl
                        placeholder="Search by name"
                        aria-label="Search by name"
                        aria-describedby="basic-addon2"
                      />
                      <InputGroup.Append>
                        <Button type="submit">Submit</Button>
                      </InputGroup.Append>
                    </InputGroup>
                  </Col>
                  <Col md={{ span: 6, offset: 1 }}>
                    <Row>
                      <Col md={6}>
                        <Form.Control type="text" placeholder="Start Date" />
                      </Col>
                      <Col md={6}>
                        <Form.Control type="text" placeholder="End Date" />
                      </Col>
                    </Row>
                  </Col>
                </Row>
              </div>
              <List data={campaigns} loading={fetching} />
            </Col>
            <Col>
            </Col>
          </Row>
        </div>                   
      </div>
    )
  }
}

const mapStateToProps = (state: State) => ({
  fetching: state.fetching,
  campaigns: state.campaigns,
})

const mapDispatchToProps = (dispatch: Dispatch<any>) => {
  return {
    dispatch
  }
}

const App = connect(
  mapStateToProps,
  mapDispatchToProps
)(AppComponent)

export default App
